import React, { Suspense, lazy, useEffect, useState } from 'react'
import styled from 'styled-components'
import { useContext } from 'react'
import { UserContext } from '../../store/UserContext'
import { BrowserContext } from '../../store/BrowserContext'
import Option from './ProfileScreen/UI.js/Option'
import { TranslationContext } from '../../store/TranslationContext'
import SuspenseComponent from '../../Components/SuspenseComponent'

const ProfileImage = <SuspenseComponent Component={lazy(()=>import('./ProfileScreen/ProfileImage'))} />
const UsernameUpdate = <SuspenseComponent Component={lazy(()=>import('./ProfileScreen/UsernameUpdate'))} />
const AccountInfo = <SuspenseComponent Component={lazy(()=>import('./ProfileScreen/AccountInfo'))} />
const PageInfo  = <SuspenseComponent Component={lazy(()=>import('./ProfileScreen/PageInfo'))} />
const UserInfo  = <SuspenseComponent Component={lazy(()=>import('./ProfileScreen/UserInfo'))} />
const LocationInfo  = <SuspenseComponent Component={lazy(()=>import('./ProfileScreen/LocationInfo'))} />
const WebsiteAndSocials  = <SuspenseComponent Component={lazy(()=>import('./ProfileScreen/WebsiteAndSocials'))} />

const StyledComponent = styled.div`
& .user-profile {
  margin: 0 ;
  padding: 1rem;
}
& .user-profile .user-avatar {
  margin: 0;
}
& .user-profile .user-avatar span {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  display: block;
  margin: auto;
  
}
& .about {
  font-size: 0.8rem;
}
& label {
  padding: 5px;
}
& .form-group{
  padding: 5px;
}
`
const OptionsContainer = styled.div`
.option:hover,
.option:active{
  background-color: var(--theme);
  color: var(--white);
  cursor: pointer;
}
`
const ProfileScreen = () => {
    const {userData} = useContext(UserContext)
    const [activeSection, setActiveSection] = useState(null)
    const {Left} = useContext(BrowserContext)
    const {translate}=useContext(TranslationContext)
    const {setTitle, setDescription} = useContext(BrowserContext)
    useEffect(()=>{
        setTitle(translate('Profile') + ` | ${translate('Ch7al Machya')}`) // Translate this
        setDescription(translate('description'))
    },[])
    return (
    <>
      {!activeSection && 
        <div className='container mx-700 p-md-3 p-2'>
          <OptionsContainer className='card'>
            <Option icon="fa-solid fa-image" title={translate('Profile image')} setActiveSection={setActiveSection} id={1} />
            <Option icon="fa-solid fa-pen-to-square" title={translate('Username')} setActiveSection={setActiveSection} id={2} />
            <Option icon="fa-solid fa-file-invoice" title={translate('Account information')} setActiveSection={setActiveSection} id={3} />
            { userData.extention.is_page ?
              <Option icon="fa-solid fa-flag fa-rotate-by" title={translate('Page information')} setActiveSection={setActiveSection} id={4}/>:
              <Option icon="fa-solid fa-user" title={translate('User information')} setActiveSection={setActiveSection} id={4}/>
            }
            <Option icon="fa-solid fa-location-dot" title={translate('Location')} setActiveSection={setActiveSection} id={5} />
            <Option icon="fa-brands fa-facebook" title={translate('Socials')} setActiveSection={setActiveSection} id={6} />
            
          </OptionsContainer>
        </div>
      }

      { activeSection && 
          <StyledComponent className='d-flex flex-column'>
          <button onClick={()=>setActiveSection(null)} className='m-2 mt-3 d-flex gap-2 align-items-center btn btn-outline-success w-fit-content border-0'>
            <i className={`fa-solid fa-chevron-${Left} fa-xl justify-self-end`}></i>
            <h4>{translate('Go back')}</h4>
          </button>
          { activeSection == 1 && ProfileImage}
          { activeSection == 2 && UsernameUpdate}
          { activeSection == 3 && AccountInfo}
          { activeSection == 4 && 
            (userData.extention.is_page ? PageInfo : UserInfo )
          }
          { activeSection == 5 && LocationInfo}        
          { activeSection == 6 && WebsiteAndSocials }
        </StyledComponent>
      }
    </>
  )
}

export default ProfileScreen