import React, { useContext } from 'react'
import { BrowserContext } from '../../../../store/BrowserContext'

const Option = ({setActiveSection, title, icon, id}) => {
    const {Right} = useContext(BrowserContext)
  return (
    <>
        <div className='py-4 px-4 d-flex align-items-center gap-3 option' onClick={()=>{setActiveSection(id)}}>
            <i className={icon + " fa-xl"}></i>
            <h5 className='flex-grow-1'>{title}</h5>
            <i className={`fa-solid fa-chevron-${Right} fa-xl justify-self-end`}></i>
        </div>
        { id != 6 && <hr className='m-0'/>}
    </>
    
  )
}

export default Option